<template>
  <div>
    <cms-breadcrumb to="/shopMember/list" type='list' @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt>
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="会员头像" prop="resourcesSpaceData">
          <cms-picture-zone  @change="getImgUrl" v-model="formData.resourcesSpaceData"></cms-picture-zone>
        </el-form-item>
        <el-form-item label="会员账户" prop="username" :error='errorMessage.username'>
          <el-input  v-model="formData.username" maxlength="12"><!-- :autofocus="true" ref="first" -->
          </el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password" :error='errorMessage.password'>
          <el-input type="password" v-model="formData.password"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="pwd" :error='errorMessage.pwd'>
          <el-input  type="password" v-model="formData.pwd"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email" :error='errorMessage.email'>
          <el-input v-model="formData.email" maxlength="30"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="phone">
          <el-input v-model="formData.phone" maxlength="11"></el-input>
        </el-form-item>
        <el-form-item label="昵称" prop="nickname">
          <el-input v-model="formData.nickname" maxlength="30"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="gender">
          <el-radio-group v-model="formData.gender">
            <el-radio :label="1">男</el-radio>
            <el-radio :label="2">女</el-radio>
            <el-radio :label="3">保密</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="生日" prop="birthday">
          <el-date-picker  :picker-options="pickerOptions" v-model="formData.birthday"
          type="date" size="small" placeholder="年/月/日">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="是否启用" prop="enabled">
          <cms-switch v-model="formData.enabled" :activeValue="1" :inactiveValue="0"></cms-switch>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(true)" v-color>保存</cms-button>
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import { memberSave, checkUserName, checkEmail, checkPhone } from '@/api/users/user.js'
var defaultData = {
  enabled: 1,
  username: '',
  password: '',
  email: '',
  phone: '',
  nickname: '',
  pwd: '',
  resourceDataId: '',
  birthday: '',
  gender: 1
}
export default {
  mixins: [form],
  data() {
    var isEmail = (rule, value, callback) => {
      // 验证邮箱唯一
      checkEmail({ email: value }).then(res => {
        if (res.code === 200) {
          if (res.data) {
            callback()
          } else {
            callback(new Error('邮箱已存在，不允许重复添加'))
          }
        }
      })
    }
    var isPhone = (rule, value, callback) => {
      // 验证电话唯一
      const reg = /^1[3456789]\d{9}$/
      if (value === '') {
        callback()
      } else {
        if (!reg.test(value)) {
          callback(new Error('手机号码格式不正确'))
        } else {
          checkPhone({ phone: value }).then(res => {
            if (res.code === 200) {
              if (res.data) {
                callback()
              } else {
                callback(new Error('手机已存在，不允许重复添加'))
              }
            }
          })
        }
      }
    }
    var isNickname = (rule, value, callback) => {
      const reg = /^([\u4E00-\u9FA5]+|[a-zA-Z]+)$/
      if (value === '') {
        callback()
      } else {
        if (!reg.test(value)) {
          callback(new Error('姓名格式不正确'))
        } else {
          callback()
        }
      }
    }
    var isPwd = (rule, value, callback) => {
      if (value === this.formData.password) {
        callback()
      } else {
        callback(new Error('密码输入不一致，请重新输入'))
      }
    }
    var isUser = (rule, value, callback) => {
      const patrnUser = /((?=[\x21-\x7e]+)[^A-Za-z0-9])/
      // 验证账户唯一
      if (value === '') {
        callback(new Error('请输入用户名'))
      } else {
        if (patrnUser.test(value)) {
          callback(new Error('用户名格式不正确'))
        } else {
          checkUserName({ username: value }).then(res => {
            if (res.code === 200) {
              if (res.data) {
                callback()
              } else {
                callback(new Error('账户已存在，不允许重复添加'))
              }
            }
          })
        }
      }
    }
    return {
      imageUrl: '',
      formData: Object.assign({}, defaultData),
      rules: {
        email: [rules.required('此项必填'), rules.email(), rules.custom(isEmail)],
        phone: [rules.custom(isPhone), rules.phone()],
        pwd: [rules.required('此项必填'), rules.custom(isPwd), rules.len(6, 16)],
        gender: [rules.required('此项必填')],
        username: [rules.required('此项必填'), rules.custom(isUser)],
        status: [rules.required('此项必填')],
        password: [rules.required('此项必填'), rules.len(6, 16)],
        nickname: [rules.custom(isNickname)]
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now() - 8.64e7
        }
      }
    }
  },
  methods: {
    getImgUrl(data, index) {
      // 获取图片地址
      this.resourcesSpaceData = data[0]
      this.formData.resourceDataId = data[0].id
    },
    save(state) {
      var queryData = {
        member: {
          enabled: this.formData.enabled,
          username: this.formData.username,
          password: this.formData.password,
          email: this.formData.email,
          phone: this.formData.phone,
          nickname: this.formData.nickname
        },
        memberExt: {
          resourceDataId: this.formData.resourceDataId,
          birthday: this.formData.birthday,
          gender: this.formData.gender
        }
      }
      queryData.member.password = this.$cms.encrypt(queryData.member.password)

      this.$refs.form.validate((valid, item) => {
        if (valid) {
          this.saveForm(memberSave, queryData, state, '/shopMember/list', 'list')
        } else {
          return
        }
      })
    }
  },
  created() {}
}
</script>

<style>
</style>



